
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Alert from 'vux/src/components/alert/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Picker from 'vux/src/components/picker/index.vue'


export default {

components: {
XHeader,
GroupTitle,
Flexbox,
FlexboxItem,
XButton,
DatetimePlugin,
Datetime,
Group,
Picker,

},
data() {
return {
value1: '',
startTime:"",
endTime:'',
hourListValue:'',
hot:'',
ListValue:'',
activeContent:'',
listSingle:{},
userName:''
};
},
methods: {
getList(){
axios.get('/dangjian/pscoredetail/queryById',{
params:{id:this.$route.params.moduleId.id}
}).then(res =>{
this.listSingle=res.data
console.log(this.listSingle)
}).catch(err =>{
console.log('fail'+err.data)

})
},
getUser() {
axios.get('/dangjian/ppartymember/queryByUserId', {
params: {
userid: this.$store.getters.user.userid
}
})
.then(res => {
this.userName = res.data.name;
})
.catch(err => {
console.log(err);
});
},
openPicker() {
this.$refs.picker.open();
},


submit(){
this.$http.post('/dangjian/pstudy/save',{
departmentid:this.$store.getters.user.departmentid,
picIds:1,
createUserid:this.$store.getters.user.userid,
roleid:this.$store.getters.user.roleid,
starttime:this.startTime,
endtime:this.endTime,
projectid:this.listSingle.projectId,
moduleid:this.listSingle.id,
content:this.activeContent
}).then(res =>{
console.log(res.data)
}).catch(err =>{
console.log('fail')
console.log(err.data)

})
},

getActivity(){
},
log (str1, str2 = '') {
console.log(str1, str2)
},
showPlugin () {
this.$vux.datetime.show({
cancelText: '取消',
confirmText: '确定',
format: 'YYYY-MM-DD HH',
value: '2017-05-20 18',
onConfirm (val) {
console.log('plugin confirm', val)
},
onShow () {
console.log('plugin show')
},
onHide () {
console.log('plugin hide')
}
})
},
toggleFormat () {
this.format = this.format === 'YYYY-MM-DD HH:mm' ? 'YYYY-MM-DD' : 'YYYY-MM-DD HH:mm'
},
changeStart (value) {
this.startTime = value;
},
changeEnd (value) {
this.endTime = value;
},
clearValue (value) {
this.value6 = ''
},
clearValue8 (value) {
this.value8 = ''
},
setToday (value) {
let now = new Date();
let cmonth = now.getMonth() + 1;
let day = now.getDate();
if (cmonth < 10) cmonth = '0' + cmonth;
if (day < 10) day = '0' + day;
this.value7 = now.getFullYear() + '-' + cmonth + '-' + day;
console.log('set today ok')
},
},
mounted() {
this.getActivity();
this.getList();
this.getUser();
console.log(this.$store.getters.user)
}
};



